import React from "react";

const Table = ({ items, onSort }) => (
  <table className="table align-items-center">
    <thead>
      <tr>
        <th scope="col" onClick={() => onSort("name")}>
          Name
        </th>
        <th scope="col" onClick={() => onSort("category")}>
          Category
        </th>
        <th scope="col">Description</th>
      </tr>
    </thead>
    <tbody className="list">
      {items.map((item, index) => (
        <>
          <tr key={index}>
            <th scope="row" className="left-pill">
              <a href={"https://docs.trusource.io/" + item.name}>{item.name}</a>
            </th>
            <th>{item.category}</th>
            <th className="right-pill">{item.description}</th>
          </tr>
          <tr className="table-divider"></tr>
        </>
      ))}
    </tbody>
  </table>
);

export default Table;
